import Header from '@/components/Header';
import HeroSection from '@/components/HeroSection';
import AboutSection from '@/components/AboutSection';
import BeforeAfterCarousel from '@/components/BeforeAfterCarousel';
import WhyChooseSection from '@/components/WhyChooseSection';
import TrainingTypesSection from '@/components/TrainingTypesSection';
import TestimonialsSection from '@/components/TestimonialsSection';
import PlansSection from '@/components/PlansSection';
import PaymentSection from '@/components/PaymentSection';
import Footer from '@/components/Footer';
import WhatsAppFloat from '@/components/WhatsAppFloat';
import BannerStrip from '@/components/BannerStrip';

export default function Home() {
  return (
    <main className="min-h-screen">
      <Header />
      
      <section id="inicio">
        <HeroSection />
      </section>
      
      {/* Banner repetido para reforço de mensagem */}
      <BannerStrip text="É HORA DE EMAGRECER" variant="light" />
      
      {/* Reordenado para priorizar resultados como no site referência */}
      <section id="resultados">
        <BeforeAfterCarousel />
      </section>
      
      <BannerStrip text="Resultados Reais • Acompanhamento Verdadeiro" variant="dark" />
      
      <section id="diferenciais">
        <WhyChooseSection />
      </section>
      
      {/* Pagamento antes de outras seções para seguir fluxo do site referência */}
      <section id="contato">
        <PaymentSection />
      </section>
      
      {/* Seções adicionais mantidas abaixo */}
      <section id="treinos">
        <TrainingTypesSection />
      </section>
      
      <section id="depoimentos">
        <TestimonialsSection />
      </section>
      
      {/* Se desejar, podemos recolocar Sobre e Planos depois */}
      {/*
      <section id="sobre">
        <AboutSection />
      </section>
      
      <section id="planos">
        <PlansSection />
      </section>
      */}
      
      <Footer />
      
      <WhatsAppFloat />
    </main>
  );
}